.ivu-layout-header {
  background: #386124;
  .ivu-menu-dark {
    background: #386124;
    .ivu-menu-item {
      color: #fff;
      font-size: 16px;
      font-weight: bold;
    }
  }
}

.ivu-menu-dark.ivu-menu-horizontal .ivu-menu-item-active {
  color: none;
}
.isActive.ivu-menu-item.ivu-menu-item-active.ivu-menu-item-selected,
.ivu-menu-dark.ivu-menu-horizontal .ivu-menu-item:hover {
  color: #f9f775;
}
