.ivu-menu-submenu-title > i {
  margin-right: 0px;
}
/deep/.ivu-select-dropdown {
  background-color: #386124 !important;
}
.ivu-menu-horizontal
  .ivu-menu-submenu
  .ivu-select-dropdown
  .ivu-menu-item:hover {
  background: #3c3c3c;
}
@media (max-width: 768px) {
  .ivu-layout-header {
    padding: 0 5px;
